import React, {Component}from 'react';
import factory from "../ethereum/factory";
import { Card, Button } from 'semantic-ui-react';



class ProductTrackIndex extends Component {
static async getInitialProps(){
    const tracks = await factory.methods.getDeployedTracks().call();
return{tracks};

}

renderIndex(){


return(
    <Card.Group>
    <Card>
      <Card.Content>
        <Card.Header>Add New Product</Card.Header>
        <Card.Description>
        Add a new product to track on the blockachain
        </Card.Description>
      </Card.Content>
      <Card.Content extra>
       
          <Button 
          content = "Add Product"
          icon= "add circle"
          />
            
         
          
      
      </Card.Content>
    </Card>

   <Card>
      <Card.Content>
        <Card.Header>Add New Location</Card.Header>
        <Card.Description>
       Update a product's location
        </Card.Description>
      </Card.Content>
      <Card.Content extra>
       
          <Button fluid primary color='brown' icon = 'map marker alternate'>
            Add Product
          </Button>
          
      
      </Card.Content>
    </Card>

    <Card>
        <Card.Content>
        <Card.Header>Track a Product</Card.Header>
        <Card.Description>
      Track the location history of a product
        </Card.Description>
        </Card.Content>
        <Card.Content extra>
        
            <Button  fluid primary color='blue' icon = 'find'>
            Add Product
            </Button>
            
        
        </Card.Content>
    </Card>
  </Card.Group>
)
}
 
 render(){

    return( 
        <div >
        <link rel="stylesheet" href="//cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.3.1/semantic.min.css"></link>
        <h1> Welcome To the Product Tracking on the Ethereum Blockchain</h1>
        {this.renderIndex()} 
        </div>)
 }

}
export default ProductTrackIndex;


